.contenedor-testimonio{
    width: 1170;
    height: 368;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: antiquewhite;
    box-shadow: 0 3px 13px 1px rgb(red, green, blue /9%);
}
.imagen-testimonio{
    height: 368px;
    width: 500px;
    object-fit: cover;
    object-position: 10% 50%;
}
.contenedor-texto-testimonio{
    width: 100%;
    font-size: 18px;

    text-align: center;
    padding: 40px;
    font-family:Lato,sans-serif ;
    font-weight: 200;
    line-height: 1.5rem;

}
.nombre-testimonio{
    font-size:1.3rem;
}
.cargo-testimonio{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}
.texto-testimonio{
    font-size:1.1rem;
    text-align: justify;
}